import { getMusicUrl } from '../../request/api/item';
import { computed, onMounted, reactive } from '@vue/runtime-core';
import { useStore, mapState } from 'vuex';
export default {
  name: 'footerMusic',
  setup() {
    const store = useStore();
    let audioUrl = reactive({
      src: '',
      bfzt: true
    });
    console.log();
    // console.log(this)
    onMounted(async () => {
      let musicUrl = await getMusicUrl(store.state.playList[store.state.playListIndex].id);
      audioUrl.src = musicUrl.data.data[0].url;
      // console.log('url', musicUrl.data.data[0].url)
      // console.log('url2', audioUrl)
    });

    return {
      store,
      audioUrl
    };
    console.log("2", store);
  },
  computed: {
    ...mapState(['playList', 'playListIndex'])
  },
  methods: {
    play: function () {
      // console.log('play', this.$refs.audio)
      this.$refs.audio.play();
      this.audioUrl.bfzt = false;
    },
    pause: function () {
      // console.log('pause', this.$refs.audio)
      this.$refs.audio.pause();
      this.audioUrl.bfzt = true;
    }
  }
};