import { useRouter } from 'vue-router';
import { onMounted, reactive } from 'vue';
import { getMusicItemList, getMusicItem } from '../request/api/item.js';
import { useStore } from 'vuex';
import itemMusicTop from '../components/item/itemMusicTop.vue';
import itemMusicList from '../components/item/ItemMusicList.vue';
export default {
  name: 'itemMusic',
  components: {
    itemMusicTop,
    itemMusicList
  },
  setup() {
    const store = useStore();
    const state = reactive({
      playList: {},
      tracks: {}
    });
    onMounted(async () => {
      // console.log(useRouter())
      let id = useRouter().currentRoute.value.query.id;
      // console.log(id)
      let responselist = await getMusicItemList(id);
      // console.log('axios接收到的值', response.data.playlist)
      state.playList = responselist.data.playlist;
      // sessionStorage.setItem('itemDetail', JSON.stringify(state))
      // console.log(state)
      let responseitem = await getMusicItem(id);
      state.tracks = responseitem.data.songs;
      // console.log('hhh', state.tracks)
      // console.log('res', state.playList)   

      sessionStorage.setItem('itemDetail', JSON.stringify(state));
      store.state.playingList = JSON.parse(sessionStorage.getItem('itemDetail'));
      console.log('hh', store, JSON.parse(sessionStorage.getItem('itemDetail')));
    });
    return {
      state
    };
  }
};