import { reactive } from '@vue/reactivity';
import { onMounted } from '@vue/runtime-core';
export default {
  name: 'itemMusicTop',
  setup(props) {
    function changCount(num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(1) + '亿';
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + '万';
      } else if (num < 10000) {
        return num;
      }
    }
    let musicTracks = reactive({
      tracks: []
    });

    // console.log('子组件接收到的父组件的值', props)
    if (props.playList.creator = "") {
      // sj.playList.creator = JSON.parse(sessionStorage.getItem().playList).creator
      props.playList.creator = JSON.parse(sessionStorage.getItem().playList).creator;
      // console.log('hhh', props.playList.creator)
    }

    // onMounted(() => {
    //     console.log(props)
    //     musicTracks = props
    // })

    return {
      changCount,
      musicTracks
    };
  },
  props: ["playList", "tracks"]
};