import {
    createRouter,
    createWebHashHistory
} from "vue-router";

import appHome from '../views/Home.vue'
import itemMusic from '../views/ItemMusic.vue'

const routes = [{
        path: '/',
        name: 'appHome',
        component: appHome
    },
    {
        path: '/itemMusic',
        name: 'itemMusic',
        component: itemMusic

    }
]


const router = createRouter({
    history: createWebHashHistory(process.env.BASE_URL),
    routes
})


export default router