export default {
  name: 'itemMusicList',
  props: ["tracks", "playList"],
  setup(props) {
    // let i = 0
    const store = useStore();
    function changCount(num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(1) + '亿';
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + '万';
      } else if (num < 10000) {
        return num;
      }
      // console.log('a', props.playList)
    }

    return {
      changCount
    };
  },
